import axios from "axios";
import Vue from "vue";
import store from "../index";

const state = {
    tokenType: null,
    token: null,
    refreshToken: null,
    user: {},
    facebook: {},
    redirectUrl: null
};

const getters = {
    token: state => {
        return state.token;
    },
    tokenType: state => {
        return state.tokenType;
    },
    refreshToken: state => {
        return state.refreshToken;
    },
    isLoggedIn: state => roles => {
        return !!state.token && roles.includes(state.tokenType);
    },
    facebookData: state => state.facebook,
    redirectUrl: state => state.redirectUrl,
};

const actions = {
    async logout({commit, state}, token) {
        return await axios.post('/logout', { logout: true }).then(resp => {
            commit('logout');
            delete axios.defaults.headers.common['Authorization'];

            setTimeout(() => {
                window.localStorage.removeItem('Eternity');

                let redirectUrl = window.location.origin;

                if(window.location.origin.indexOf('eternity.co.il') != -1 && window.location.pathname != '/dashboard') {
                    let url = decodeURIComponent(window.location.origin +'/auth/handle');
                    redirectUrl = 'https://eternity.co.il/sso?type=login&lang='+localStorage.getItem('vueml-lang')+'&redirectURL='+url;
                }

                window.location.href=redirectUrl;
            }, 100);
        })
    },
    async login({commit, state}) {
        window.localStorage.removeItem('Eternity');

        return await axios.post('/login/customers', {
                eternityToken: null
            })
            .then(({ data }) => {
                commit('setToken', {data: data, tokenType: 'customer'});
            })
            .catch(error => {
                if(error.response !== undefined) {
                    alert(error.response.data.errorDescription);
                }
                else {
                    alert(error.message);
                }
            })
    },
    async loginTest({commit, state}) {
        window.localStorage.removeItem('Eternity');

        await axios.post('/login/customers', {
                eternityToken: 'f38f90246270721484b7a70675c7aaea'
            })
            .then(({ data }) => {
                // console.log(data);
                commit('setToken', {data: data, tokenType: 'customer'});
            })
            .catch(error => {
                //console.log(error);
                if(error.response !== undefined) {
                    alert(error.response.data.errorDescription);
                }
                else {
                    alert(error.message);
                }
            })
    },
    getToken({commit, state}) {
        axios.get('/token')
            .then(res => {
                // console.log(res)
                commit('setToken', {data: res.data, tokenType: 'anonymous'});
            })
            .catch(error => {
                //console.log(error);
            })
    },
    refreshToken({commit, state}) {
        axios.post('/token/refresh', {
                refreshToken: localStorage.getItem('refreshToken')
            })
            .then(res => {
                commit('setToken', {data: res.data});
            })
            .catch(error => {
                //console.log(error);
            })
    },
    facebook({commit, state}, data) {
        commit('setFacebookData', data);
    },
    setToken({commit, state}, {data, tokenType = null}) {
        commit('setToken', {data, tokenType});
    },

    async relativeLogin({ commit, state }, payload) {
        return await new Promise((resolve, reject) => {
            axios({url: `/login/memorials/${payload.memorialId}/relations`, data: payload.data, method: 'POST'})
                .then(response => {
                    resolve(response);
                })
                .catch(error => {
                    reject(error);
                })
        });
    },
};

const mutations = {
    setToken(state, {data, tokenType = null}) {
        if(data === undefined || !data.token) {
            Vue.set(state, 'token', null);
            Vue.set(state, 'refreshToken', null);
            Vue.set(state, 'tokenType', null);

            axios.defaults.headers.common['Authorization'] = '';
        } else {
            Vue.set(state, 'token', data.token);
            Vue.set(state, 'refreshToken', data.refreshToken);

            if(null !== tokenType) {
                Vue.set(state, 'tokenType', tokenType);
            }

            axios.defaults.headers.common['Authorization'] = `Bearer ${data.token}`
        }
    },
    setAuthUser(state, user) {
        state.user = user;
    },
    authRequest(state) {
        state.status = 'loading'
    },
    authSuccess(state, token) {
        state.status = 'success'
        state.token = token
    },
    authError(state) {
        state.status = 'error'
    },
    logout(state) {
        state.status = ''
        state.token = ''
        state.user = {}
    },
    setFacebookData(state, data) {
      state.facebook = {
          accessToken: data.accessToken,
          userID: data.userID,
      };
    },
    setRedirectUrl(state, url) {
        state.redirectUrl = url
    },
};

export default {
    namespaced: true,
    state,
    getters,
    actions,
    mutations,
};
