import axios from 'axios';

const state = {
    user: null,
};

const getters = {
    getUser: state => {
        return state.user;
    },
};

const actions = {
    me({commit, dispatch}, userId) {
        axios.get('/customers/me')
            .then(resp => {
                // if(resp.data !== undefined) {
                    commit('setUser', resp.data);
                // }
            })
            .catch(error => {
                // if(error.response !== undefined && error.response.data !== undefined) {
                //     alert(error.response.data.errorDescription);
                // }
                // else {
                //     alert(error.message);
                // }

                // global.dispatch('auth/logout');
                dispatch('auth/logout', null, {root:true});
            })
    },
};

const mutations = {
    setUser(state, user) {
        state.user = user;
    },
};

export default {
    namespaced: true,
    state,
    getters,
    actions,
    mutations,
};
