import axios from 'axios';
import Vue from "vue";

const state = {
    periods: [],
    period: [],
    errors: {
        name: null,
        location: null,
        till: null,
        since: null,
    },
};

const getters = {
    getErrors: state => {
        return state.errors;
    },
    getPeriods: state => {
        const periods = Object.assign([], state.periods);
        return periods.sort((a, b) => Number(a.isGeneral) < Number(b.isGeneral) ? 1 : -1);
    },
    getPeriodsFiltered: state => {
        return state.periods.filter(item => item.all !== undefined && item.all.length);
    },
    getPeriodsDefault: state => {
        return state.periods;
    },
    getPeriodEdit: state => {
        return state.period;
    },
    getYears: (state,getters) => {
        let years = [];
        let periods = getters.getPeriodsWithYears;

        for(let i in periods) {
            let item = periods[i];

            let since = new Date(item.since);
            since = since.getFullYear();

            let till = new Date(item.till);
            till = till.getFullYear();

            years.push(`${since} - ${till}`);
        }

        return years;
    },
    getPeriodsWithYears: (state,getters) => {
        return getters.getPeriodsFiltered.filter(item => item.since !== null);
    },
    getPeriodsWithoutYears: (state,getters) => {
        return getters.getPeriodsFiltered.filter(item => item.since === null);
    },
    getTimelineRange: (state,getters) => {
        let periods = getters.getPeriodsFiltered.filter(item => item.since !== null);

        if (periods.length == 0) {
            return '';
        }

        if (periods.length == 1) {

            let since = new Date(periods[0].since);
            since = since.getFullYear();

            let till = new Date(periods[0].till);
            till = till.getFullYear();

            return `${since} - ${till}`;
        }
        if (periods.length > 1) {

            let since = new Date(periods[0].since);
            since = since.getFullYear();

            let till = new Date(periods[periods.length-1].till);
            till = till.getFullYear();

            return `${since} - ${till}`;
        }
    },
};

const actions = {
    create({ commit }, payload) {
        return new Promise((resolve, reject) => {
            axios({url: `/memorials/${payload.memorial}/periods`, data: payload.data, method: 'POST'})
                .then(response => {
                    resolve({
                        status: true,
                    });
                })
                .catch(error => {
                    // console.log('error', error.response)
                    resolve({
                        status: false,
                        data: error.response,
                    });
                })
        });
    },
    update({ commit }, payload) {
        return new Promise((resolve, reject) => {
            axios({url: `/memorial-periods/${payload.id}`, data: payload.data, method: 'PUT'})
                .then(response => {
                    resolve({
                        status: true,
                        data: response.data
                    });
                })
                .catch(error => {

                    if(error.response !== undefined && (error.response.status != 400 && error.response.status != 422))
                    {
                        alert(error.response.data.errorDescription);
                    }
                    else if(typeof error.data.data.violations.detail !== 'undefined') {
                        alert(error.data.data.violations.detail);
                    }
                    // else if(error.response !== undefined && error.response.data.violations !== undefined && error.response.data.violations.violations === undefined) {
                    //     commit('setErrors', error.response.data.violations);
                    // }
                    // else if(error.response !== undefined && error.response.data.violations.violations !== undefined) {
                    //     commit('setErrorsType2', error.response.data.violations.violations);
                    // }
                    else {
                        alert(error.message);
                    }

                    resolve({
                        status: false,
                        data: error.response,
                    });
                })
        });
    },
    getPeriods({ commit, state }, payload) {
        return axios.get(`/memorials/${payload.id}/periods`+(payload.storyType !== undefined ? `/${payload.storyType}` : ''), {
            params: {
                withGeneral: typeof payload.withGeneral !== 'undefined' ? payload.withGeneral : true,
            },
        })
            .then(({ data }) => {
                if(payload.storyType == 'all') {
                    for(let p in data) {
                        for(let l in data[p].all) {
                            let imageLast = -1;

                            for(let c in data[p].all[l].content) {
                                let content = data[p].all[l].content[c];

                                if(content.contentType == 'image') {
                                    imageLast = c;
                                    data[p].all[l].content[c].last = false;
                                }
                            }

                            if(imageLast > -1) {
                                data[p].all[l].content[imageLast].last = true;
                            }
                        }
                    }
                }

                if(payload.selected) {
                    let data2 = data.find(i => i.id === payload.selected);
                    data = [];
                    data.push(data2)
                }

                commit('setPeriods', data);
            })
            .catch(error => {
                //console.log(error);
            })
            .finally(() => {
            });
    },

    getPeriodEditStories({ commit, state }, payload) {
        return axios.get(`/memorial-periods/${payload.id}/edit-stories`)
            .then(({ data }) => {

                for(let l in data.all) {
                    let imageLast = -1;

                    for(let c in data.all[l].content) {
                        let content = data.all[l].content[c];

                        if(content.contentType == 'image') {
                            imageLast = c;
                            data.all[l].content[c].last = false;
                        }
                    }

                    if(imageLast > -1) {
                        data.all[l].content[imageLast].last = true;
                    }
                }

                commit('setPeriodEdit', data);
            })
            .catch(error => {
                //console.log(error);
            })
            .finally(() => {
            });
    },

};

const mutations = {
    setPeriods(state, data) {
        state.periods = data;
    },
    setPeriodEdit(state, data) {
        state.period = data;
    },
    setPeriodData(state, data) {
        Vue.set(state.periods[0], 'name', data.name);
        Vue.set(state.periods[0], 'till', data.till);
        Vue.set(state.periods[0], 'since', data.since);
        Vue.set(state.periods[0], 'editVersion', data.editVersion);
    },
    setPeriodStoryVisible(state, story) {
        state.period.all.map(i => i.id == story.id ? i.visible = story.visible : i.visible = i.visible);
    },

    setErrorsType2(state, data) {
        if(data === undefined) {
            state.errors = {
                name: null,
                location: null,
                till: null,
                since: null,
            };

            return;
        }

        for(let i in data) {
            let errorKey = data[i].propertyPath;

            if (errorKey && state.errors[errorKey] !== undefined) {
                state.errors[errorKey] = data[i].title;
            }
        }
    },
    setErrors(state, data) {
        if(data === undefined) {
            state.errors = {
                name: null,
                location: null,
                till: null,
                since: null,
            };

            return;
        }

        for(let key in data) {
            for(let i in data[key]) {
                let error = data[key][i];
                let errorKey = Object.keys(error)[0];

                if (error && errorKey && state.errors[errorKey] !== undefined) {
                    state.errors[errorKey] = error[errorKey];
                }
            }
        }
    },
};

export default {
    namespaced: true,
    state,
    getters,
    actions,
    mutations,
};
