import Vue from 'vue';
import axios from 'axios';

const state = {
  isMemorialExist: false,
  memorial: {},
  users: {},
  user: {
    name: '',
    photo: {
      id: null,
      url: null,
      status: null,
    },
    relation: '',
  },
  stories: [],
  editStories: [],
  story: {},
  shareText: null,
};

const getters = {
  isMemorialExist: state => {
    return state.isMemorialExist;
  },
  memorial: state => {
    return state.memorial;
  },
  user: state => {
    return state.user;
  },
  users: state => {
    if(typeof state.memorial.relations !== 'undefined') {
      let users = state.memorial.relations.map(relation => {
        let photo = {
          id: null,
          url: null,
          status: null,
        };

        if(null !== relation.user.profilePhoto && typeof relation.user.profilePhoto !== 'undefined') {
          photo = relation.user.profilePhoto;
        }
        return {
          label: relation.user.name,
          value: relation.user.name,
          photo: photo,
        }
      });

      return users.filter((user, index, self) => self.findIndex(t => t.label === user.label) === index)
    }

    return [];
  },
  relativeUsers: state => {
    if(typeof state.memorial.relations !== 'undefined') {
      return state.memorial.relations.map(relation => {
        relation.user['relationToMemorialPerson'] = relation.relationToMemorialPerson;
        return relation.user;
      });
    }

    return [];
  },
  getStories: state => {
    return state.stories;
  },
  editStories: state => {
    return state.editStories;
  },
  shareText: state => {
    return state.shareText;
  },
  story: state => {
    return state.story;
  },
};

const actions = {
  async getMemorial({ commit }, slug) {
    return await new Promise((resolve, reject) => {
      axios({url: `/public-memorials/${slug}`, method: 'GET'})
        .then(response => {
          commit('setMemorial', response.data);
          commit('setMemorialExist', true);
          resolve(response);
        })
        .catch(error => {
          //console.log(error);
          commit('setMemorialExist', false);
          commit('setMemorial', {});

          if(error.response !== undefined) {
            alert(error.response.data.errorDescription);
          }
          else {
            alert(error.message);
          }

          resolve(error);
        })
    });
  },

  async getOwnerMemorial({ commit }, id) {
    return await new Promise((resolve, reject) => {
      axios({url: `/memorials/${id}`, method: 'GET'})
        .then(response => {
          commit('setMemorial', response.data);
          commit('setMemorialExist', true);
          resolve(response);
        })
        .catch(error => {
          commit('setMemorial', {});
          commit('setMemorialExist', false);

          resolve(error);
        })
    });
  },

  create({ commit }, payload) {
    return new Promise((resolve, reject) => {
      axios({url: `/memorial-periods/${payload.periodId}/stories`, data: payload.data, method: 'POST'})
        .then(response => {
          resolve(true);
        })
        .catch(error => {
          // console.log('error', error)
          resolve(false);
        })
    });
  },

  approve({ commit }, {id, editVersion}) {
    return new Promise((resolve, reject) => {
      axios({url: `/memorial-stories/${id}/approve`, data: {editVersion: editVersion}, method: 'PUT'})
        .then(response => {
          resolve({
            status: true,
            data: response
          });
        })
        .catch(error => {
          resolve({
            status: false,
            data: error.response.data.errorDescription,
          });
        })
    });
  },

  setVisible({ commit }, payload) {
    return new Promise((resolve, reject) => {
      axios({url: `/memorial-stories/${payload.id}/${payload.data.visible ? 'show' : 'hide'}`, data: {editVersion: payload.data.editVersion}, method: 'PUT'})
        .then(response => {
          resolve(response.data);
        })
        .catch(error => {
          resolve(false);
        })
    });
  },

  updatePeriod({ commit }, payload) {
    return new Promise((resolve, reject) => {
      axios({url: `/memorial-stories/${payload.id}/period`, data: {editVersion: payload.data.editVersion, memorialPeriodId: payload.data.memorialPeriodId}, method: 'PUT'})
        .then(response => {
          resolve(response.data);
        })
        .catch(error => {
          resolve(false);
        })
    });
  },

  getPreviewLink({ commit }, url) {
    return new Promise((resolve, reject) => {
      axios.get(`/link-preview`, {
        params: {
          url: url,
        }
      })
        .then(response => {
          resolve(response.data.previewImgLink);
        })
        .catch(error => {
          //console.log(error.response)
          resolve(false);
        })
    });
  },

  getFacebookData({ commit, rootGetters }) {
    const data = rootGetters['auth/facebookData'];

    return new Promise((resolve, reject) => {
      //fixme: fix url
      axios({url: `https://graph.facebook.com/v9.0/${data.userID}?fields=first_name, last_name, picture.type(large)&access_token=${data.accessToken}`, method: 'GET'})
          .then(response => {
          commit('saveFacebookData', response.data)
          resolve(response);
     }).catch(error => {
          reject(error);
        })
    });
  },

  saveField({ commit }, data) {
    commit('saveField', data)
  },

  getRandomStories({ commit, state }, payload) {
    return axios.get(`/memorials/${payload.id}/random-stories`, {params: {count: payload.count}})
        .then(({data}) => {
          for (let i in data) {
            if (data[i].content.length > 0) {
              data[i].content = data[i].content.splice(0, 1)
            }
          }

          commit('saveStories', data);
        })
        .catch(error => {
          commit('saveStories', []);
        })
        .finally(() => {
        });
  },

  getEditStories({ commit, state }, payload) {
    return axios.get(`/memorials/${payload.id}/edit-stories`)
        .then(({data}) => {
          commit('saveEditStories', data);
        })
        .catch(error => {
          commit('saveEditStories', []);
        })
        .finally(() => {
        });
  },

  getRelativeStories({ commit, state }, payload) {
    return axios.get(`/memorials/${payload.memorialId}/users/${payload.relativeId}/stories`)
        .then(({ data }) => {
          commit('saveStories', data);
        })
        .catch(error => {
          commit('saveStories', []);
        })
        .finally(() => {
        });
  },

  saveShareText({ commit }, text) {
    commit('setShareText', text);
  },

  saveStory({ commit }, story) {
    commit('saveStory', story);
  },

  getStory({ commit, state }, id) {
    return axios.get(`/memorial-stories/${id}`)
        .then(({ data }) => {
          commit('saveStory', data);
        })
        .catch(error => {
          commit('saveStory', {});
        })
        .finally(() => {
        });
  },

  update({ commit }, payload) {
    return new Promise((resolve, reject) => {
      axios({url: `/memorial-stories/${payload.id}`, data: payload.data, method: 'PUT'})
          .then(response => {
            resolve({
              status: true,
              data: response.data
            });
          })
          .catch(error => {
            resolve({
              status: false,
              data: error
            });
          })
    });
  },

  updateEditStories({ commit }, data) {
    commit('updateEditStories', data);
  },

  getPeriodStories({ commit, state }, payload) {
    return axios.get(`/memorial-periods/${payload.id}/stories`+(payload.storyType !== undefined ? `/${payload.storyType}` : ''))
        .then(({ data }) => {
          commit('saveStories', data);
        })
        .catch(error => {
          //console.log(error);
        })
        .finally(() => {
        });
  },
};

const mutations = {
  saveStory(state, story) {
    state.story = story;
  },

  setShareText(state, text) {
    state.shareText = text;
  },

  setMemorial(state, data) {

    if(data !== null && data.relations !== undefined) {
      data.relations.unshift(data.owner);
    }

    state.memorial = data;
  },

  setMemorialExist(state, isExist) {
    state.isMemorialExist = isExist;
  },

  saveFacebookData(state, data) {
    state.user = {
      name: `${data.first_name} ${data.last_name}`,
      photo: {
        id: null,
        url: data.picture.data.url,
        status: 'facebook',
      },
      relation: state.user.relation,
    };
  },

  saveField(state, data) {
    if (typeof state.user === 'undefined') {
      state.user = {};
    }

    if(Array.isArray(data)) {
      data.forEach(element => {
        Vue.set(state.user, element.field, element.data);
      })
    } else {
      Vue.set(state.user, data.field, data.data);
    }
  },

  saveStories(state, data) {
    state.stories = data;
  },

  saveEditStories(state, data) {
    state.editStories = data;
  },

  updateEditStories(state, data) {
    let idx = state.editStories.findIndex(el => el.id === data.id);

    if(idx >= 0) {
      Vue.set(state.editStories, idx, data);
    }
  },
};

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations,
};
