import Vue from 'vue';
import axios from 'axios';

const state = {
  memorials: {},
  stories: [],
  currentMemorial: {},
};

const getters = {
  memorials: state => {
    return state.memorials;
  },
  stories: state => {
    return state.stories;
  },
  currentMemorial: state => {
    return state.currentMemorial;
  },
  countStories: (state) => (id) => {
    let total = 0;
    for (let key in state.stories) {
      if (typeof state.stories[key] !== 'undefined' && key === id) {
        total += state.stories[key].length;
      }
    }

    return total;
  },
};

const actions = {
  async setCurrentMemorial({commit, dispatch}, memorialId) {
    await axios.get(`/memorials/${memorialId}`)
      .then(({data}) => {
        dispatch('updateCurrentMemorialData', data);
      })
      .catch(error => {
        if (error.response !== undefined && (error.response.status !== 400 && error.response.status !== 422)) {
          //console.log(error.response.data.errorDescription);
        } else {
          //console.log(error.message);
        }
      })
      .finally(() => {
      });
  },
  async memorials({commit, dispatch}) {
    return await new Promise((resolve, reject) => {
      axios({url: '/memorials', method: 'GET'})
        .then(response => {
          commit('setMemorials', response.data);

          resolve(response)
        })
        .catch(error => {
          if (typeof error.response !== 'undefined') {
            //console.log(error.response.data.errorDescription)
          } else {
            //console.log(error.message);
          }
          reject(error)
        })
    })
  },
  resetMemorials({commit, dispatch}) {
    commit('setMemorials', {});
  },
  stories({commit}, id) {
    return new Promise((resolve, reject) => {
      axios({url: `/memorials/${id}/stories/approve`, method: 'GET'})
        .then(response => {
          commit('setStories', {id, stories: response.data});
          resolve(response)
        })
        .catch(error => {
          if (typeof error.response !== 'undefined') {
            //console.log(error.response.data.errorDescription)
          } else {
            //console.log(error.message);
          }
          reject(error)
        })
    });
  },
  publishMemorial({commit, state, dispatch}, payload) {
    axios.put(`/memorials/${payload.memorialId}`, {
      editVersion: payload.editVersion,
      visible: true,
    })
      .then(({data}) => {
        dispatch('updateCurrentMemorialData', data);
      })
      .catch(error => {
        if (error.response !== undefined && error.response.data.errorDescription !== undefined) {
          alert(error.response.data.errorDescription);
        } else {
          alert(error.message);
        }
      })
      .finally(() => {
      });
  },
  sortMemorialsByCurrent({commit, state}, id) {
    const activeMemorial = state.memorials.find(el => el.id === id);
    const activeMemorialIndex = state.memorials.findIndex(el => el.id === id);
    let memorials = Object.assign([], state.memorials);
    memorials.splice(activeMemorialIndex, 1);
    memorials.unshift(activeMemorial);

    commit('setMemorials', memorials);
  },
  async updateFields({commit, dispatch}, payload) {
    await axios.put(`/memorials/${payload.memorialId}`, payload.data)
      .then(({data}) => {
        dispatch('updateCurrentMemorialData', data);
      })
      .catch(error => {
        if (error.response !== undefined && (error.response.status !== 400 && error.response.status !== 422)) {
          alert(error.response.data.errorDescription);
        } else if (error.response !== undefined && error.response.data.violations !== undefined && error.response.data.violations.violations === undefined) {
          commit('setErrors', error.response.data.violations);
        } else if (error.response !== undefined && error.response.data.violations.violations !== undefined) {
          commit('setErrorsType2', error.response.data.violations.violations);
        } else {
          alert(error.message);
        }
      })
    ;
  },
  updateCurrentMemorialData({commit, dispatch}, data) {
    commit('setCurrentMemorial', data);
    commit('resetStories');
    dispatch('stories', data.id);
  },
  updateStories({ commit }, data) {
    commit('updateStories', data);
  },
};

const mutations = {
  setMemorials(state, memorials) {
    Vue.set(state, 'memorials', memorials);
  },
  setCurrentMemorial(state, memorial) {
    state.currentMemorial = memorial;
  },
  setStories(state, {id, stories}) {
    if (typeof state.stories[id] === 'undefined') {
      Vue.set(state.stories, id, []);
    }

    Vue.set(state.stories, id, stories);
  },
  resetStories(state) {
    Vue.set(state, 'stories', []);
  },
  updateStories(state, data) {
    if(state.stories[data.memorialId]) {
      let stories = state.stories[data.memorialId];
      let idx = stories.findIndex(el => el.id === data.data.id);


      if (idx >= 0) {
        stories[idx] = data.data;
        Vue.set(state.stories, data.memorialId, stories);
      }
    }
  },
};

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations,
};
