import axios from 'axios';

const state = {
    formIsLoading: false,
    step: 1,
    isUpdated: false,
    uploadedFile: null,
    viewedFile: null,
    memorialFormData: null,
    memorialCreated: {},
    memorialForCreating: null,
    iframeUrl: null,
    shareText: null,
    errors: {
        name: null,
        slug: null,
        birthDate: null,
        deathDate: null,
        profilePhotoId: null,
        relationToMemorialPerson: null,
        relation: null,
    },
    subscription: {
        type: 'monthly', //['monthly', 'once']
        installation: 'independent', //['independent', 'team']
    },
};

const getters = {
    getIframeUrl: state => {
        return state.iframeUrl;
    },
    getShareText: state => {
        return state.shareText;
    },
    getErrors: state => {
        return state.errors;
    },
    getFile: state => {
        return state.uploadedFile;
    },
    getViewedFile: state => {
        return state.viewedFile;
    },
    getMemorialCreated: state => {
        return state.memorialCreated;
    },
    getMemorialForCreating: state => {
        return state.memorialForCreating;
    },
    getMemorialFormData: state => {
        return state.memorialFormData;
    },
    formIsLoading: state => {
        return state.formIsLoading;
    },
    getStep: state => {
        return state.step;
    },
    getIsUpdated: state => {
        return state.isUpdated;
    },
    getSubscription: state => {
      return state.subscription;
    },
};

const actions = {
    updatePaymentPlan({ commit, state }, id) {
        return axios.put(`/memorials/${id}/payment-plan`, { paymentPlan: state.subscription.type })
            .then(({ data }) => {
                if(data.errorDescription !== undefined) {
                    alert(data.errorDescription);
                }
                else {
                    commit('setIframeUrl', data)
                }
            })
            .catch(error => {
                //console.log(error);

                if(error.response !== undefined) {
                    alert(error.response.data.errorDescription);
                }
                else {
                    alert(error.message);
                }
            })
            .finally(() => {
            });
    },
    sendEmail({ commit }) {
        return axios.post('/payments-support/email', { sendEmail: true })
            .then(({ data }) => {
                if(data.errorDescription !== undefined) {
                    alert(data.errorDescription);
                }
            })
            .catch(error => {
                //console.log(error);

                if(error.response !== undefined) {
                    alert(error.response.data.errorDescription);
                }
                else {
                    alert(error.message);
                }
            })
            .finally(() => {
            });
    },
    getFile({ commit }, file) {
        return axios.get('/files/'+file.id)
            .then(({ data }) => {
                commit('setViewedFile', data);
            })
            .catch(error => {
                if(error.response !== undefined) {
                    alert(error.response.data.errorDescription);
                }
                else {
                    alert(error.message);
                }
                //console.log(error);
            })
            .finally(() => {
            });
    },
    upload({ commit, state }, data) {
        const form = new FormData();

        form.append('file', data.file);

        return new Promise((resolve, reject) => {
            axios.post(`/files/${data.type}`, form, {
                params: typeof data.memorialId !== 'undefined' && null !== data.memorialId ? {memorialId: data.memorialId} : {},
            })
              .then(({ data }) => {
                  commit('setUploadedFile', null);
                  commit('setUploadedFile', data);
                  resolve(
                    {
                        status: true,
                        data: data
                    }
                  );
              })
              .catch(error => {
                  commit('setUploadedFile', null);

                  if(typeof error.response.data.violations !== 'undefined' && typeof error.response.data.violations.detail !== 'undefined') {
                      resolve({
                          status: false,
                          data: error.response.data.violations.detail
                      });
                  } else {
                      resolve({
                          status: false,
                          data: error
                      });
                  }
              })
              .finally(() => {
              });
        });
    },
    async getMemorial({ commit, state }, id = null) {
        if(state.memorialCreated == null && !id) {
            return;
        }

        return await axios.get(`/memorials/${id || state.memorialCreated.id}`)
            .then(({ data }) => {
                commit('setMemorialCreated', data);
            })
            .catch(error => {
                // if(error.response !== undefined && (error.response.status != 400 && error.response.status != 422))
                // {
                //     alert(error.response.data.errorDescription);
                // }
                // else {
                //     alert(error.message);
                // }

                if(error.response !== undefined && error.response.status == 404) {
                    commit('setMemorialCreated', null);
                }
            })
            .finally(() => {
            });
    },
    async getMemorialForCreating({ commit, state }, id = null) {
        if(state.memorialForCreating == null && !id) {
            return;
        }

        return await axios.get(`/memorials/${id || state.memorialForCreating.id}`)
            .then(({ data }) => {
                commit('setMemorialForCreating', data);
            })
            .catch(error => {
                if(error.response !== undefined && error.response.status == 404) {
                    commit('setMemorialForCreating', null);
                }
            })
            .finally(() => {
            });
    },
    async update({ commit }, formData) {
        commit('setFormIsLoading', true);
        commit('setErrors');

        if(formData.file !== null) {
            formData.model.profilePhotoId = formData.file.id;
        }
        else {
            formData.model.profilePhotoId = null;
        }

        let removeVar = ({ relationToMemorialPerson, relation, ...rest }) => rest;
        let model = removeVar(formData.model);

        await axios.put('/memorials/'+formData.id, model)
            .then(({ data }) => {
                commit('setMemorialForCreating', data);
                commit('setMemorialFormData', formData.model);
                commit('setIsUpdated', true);
                commit('setStep', 2);
            })
            .catch(error => {
                if(error.response !== undefined && (error.response.status != 400 && error.response.status != 422))
                {
                    alert(error.response.data.errorDescription);
                }
                else if(error.response !== undefined && error.response.data.violations !== undefined && error.response.data.violations.violations === undefined) {
                    commit('setErrors', error.response.data.violations);
                }
                else if(error.response !== undefined && error.response.data.violations.violations !== undefined) {
                    commit('setErrorsType2', error.response.data.violations.violations);
                }
                else {
                    alert(error.message);
                }
            })
            .finally(() => {
                commit('setFormIsLoading', false);
            });
    },

    async updateFields({ commit }, payload) {
        await axios.put(`/memorials/${payload.memorialId}`, payload.data)
            .then(({ data }) => {
                commit('setMemorialCreated', data);
            })
            .catch(error => {
                if(error.response !== undefined && (error.response.status != 400 && error.response.status != 422))
                {
                    alert(error.response.data.errorDescription);
                }
                else if(error.response !== undefined && error.response.data.violations !== undefined && error.response.data.violations.violations === undefined) {
                    commit('setErrors', error.response.data.violations);
                }
                else if(error.response !== undefined && error.response.data.violations.violations !== undefined) {
                    commit('setErrorsType2', error.response.data.violations.violations);
                }
                else {
                    alert(error.message);
                }
            })
           ;
    },
    async create({ commit }, formData) {
        commit('setFormIsLoading', true);
        commit('setErrors');

        if(formData.file !== null) {
            formData.model.profilePhotoId = formData.file.id;
        }

        let model = { ...formData.model, relationToMemorialPerson: formData.model.relation };
        let removeVar = ({ relation, ...rest }) => rest;
        model = removeVar(model);

        await axios.post('/memorials', model)
            .then(({ data }) => {
                commit('setMemorialForCreating', data);
                commit('setMemorialFormData', formData.model);
                commit('setIsUpdated', true);
                commit('setStep', 2);
            })
            .catch(error => {
                if(error.response !== undefined && (error.response.status != 400 && error.response.status != 422))
                {
                    alert(error.response.data.errorDescription);
                }
                else if(error.response !== undefined && error.response.data.violations !== undefined && error.response.data.violations.violations === undefined) {
                    commit('setErrors', error.response.data.violations);
                }
                else if(error.response !== undefined && error.response.data.violations.violations !== undefined) {
                    commit('setErrorsType2', error.response.data.violations.violations);
                }
                else {
                    alert(error.message);
                }
            })
            .finally(() => {
                commit('setFormIsLoading', false);
            });
    },
    saveShareText({ commit }, text) {
        commit('setMemorialCreatedShareText', text);
    },
    setSubscriptionType({ commit }, type) {
        commit('setSubscriptionType', type);
    },
    setSubscriptionInstallation({ commit }, type) {
        commit('setSubscriptionInstallation', type);
    },
};

const mutations = {
    setIframeUrl(state, data) {
        state.iframeUrl = data == null ? null : data.iframeSrcUrl;
    },
    setErrorsType2(state, data) {
        if(data === undefined) {
            state.errors = {
                name: null,
                slug: null,
                birthDate: null,
                deathDate: null,
                profilePhotoId: null,
                relationToMemorialPerson: null,
                relation: null
            };

            return;
        }

        for(let i in data) {
            let errorKey = data[i].propertyPath;

            if (errorKey && state.errors[errorKey] !== undefined) {
                state.errors[errorKey] = data[i].title;
            }
        }
    },
    setErrors(state, data) {
        if(data === undefined) {
            state.errors = {
                name: null,
                slug: null,
                birthDate: null,
                deathDate: null,
                profilePhotoId: null,
                relationToMemorialPerson: null,
                relation: null,
            };

            return;
        }

        for(let key in data) {
            for(let i in data[key]) {
                let error = data[key][i];
                let errorKey = Object.keys(error)[0];

                if (error && errorKey && state.errors[errorKey] !== undefined) {
                    state.errors[errorKey] = error[errorKey];
                }
            }
        }
    },
    setFormIsLoading(state, status) {
        state.formIsLoading = status;
    },
    setUploadedFile(state, file) {
        state.uploadedFile = file;
    },
    setViewedFile(state, file) {
        state.viewedFile = file;
    },
    setMemorialCreated(state, data) {
        state.memorialCreated = data;
    },
    setMemorialForCreating(state, data) {
        state.memorialForCreating = data;
    },
    setMemorialCreatedShareText(state, text) {
        state.shareText = text;
    },
    setMemorialFormData(state, data) {
        state.memorialFormData = data;
    },
    setStep(state, step) {
        state.step = step;
    },
    setIsUpdated(state, cond) {
        state.isUpdated = cond;
    },
    setSubscriptionType(state, type) {
        state.subscription.type = type;
    },
    setSubscriptionInstallation(state, type) {
        state.subscription.installation = type;
    },
    setDefaultData(state) {
        state.formIsLoading = false;
        state.step = 1;
        state.isUpdated = false;
        state.uploadedFile = null;
        state.viewedFile = null;
        state.memorialFormData = null;
        state.memorialCreated = {};
        state.memorialForCreating = null;
        state.iframeUrl = null;
        state.errors = {
            name: null,
            slug: null,
            birthDate: null,
            deathDate: null,
            profilePhotoId: null,
            relationToMemorialPerson: null,
            relation: null,
        };
        state.subscription = {
            type: 'monthly',
            installation: 'independent',
        };
    }
};

export default {
    namespaced: true,
    state,
    getters,
    actions,
    mutations,
};
