import axios from 'axios';

const state = {
    helpFormIsLoading: 'ready',
    errors: {
        name: null,
        email: null,
        content: null,
        agree: null,
    }
};

const getters = {
    helpFormIsLoading: state => {
        return state.helpFormIsLoading;
    },
    getErrors: state => {
        return state.errors;
    },
};

const actions = {
    async sendFeedback({ commit }, formData) {
        commit('setFormIsLoading', 'loading');
        commit('setErrors');

        return await axios.post('/support/email', formData)
            .then(({ data }) => {
                // console.log(data);

                if(data.status == 'sent') {
                    commit('setFormIsLoading', 'success');
                }
                else {
                    alert(data.status);
                    commit('setFormIsLoading', 'failed');
                }
            })
            .catch(error => {
                //console.log(error);
                commit('setFormIsLoading', 'error');

                if (error.response === undefined){
                    alert(error.message);
                }
                if(error.response.data.status !== undefined) {
                    alert(error.response.data.status);
                }
                else if(error.response !== undefined && (error.response.status != 400 && error.response.status != 422))
                {
                    alert(error.response.data.errorDescription);
                }
                else if(error.response !== undefined && error.response.data.violations !== undefined && error.response.data.violations.violations === undefined) {
                    commit('setErrors', error.response.data.violations);
                }
                else if(error.response !== undefined && error.response.data.violations.violations !== undefined) {
                    commit('setErrorsType2', error.response.data.violations.violations);
                }
            })
            .finally(() => {
                // commit('setFormIsLoading', 'ready');
            });
    },
};

const mutations = {
    setFormIsLoading(state, status) {
        state.helpFormIsLoading = status;
    },
    setErrorsType2(state, data) {
        if(data === undefined) {
            state.errors = {
                name: null,
                email: null,
                content: null,
                agree: null
            };

            return;
        }

        for(let i in data) {
            let errorKey = data[i].propertyPath;

            if (errorKey && state.errors[errorKey] !== undefined) {
                state.errors[errorKey] = data[i].title;
            }
        }
    },
    setErrors(state, data) {
        if(data === undefined) {
            state.errors = {
                name: null,
                email: null,
                content: null,
                agree: null,
            };

            return;
        }

        for(let key in data) {
            for(let i in data[key]) {
                let error = data[key][i];
                let errorKey = Object.keys(error)[0];

                if (error && errorKey && state.errors[errorKey] !== undefined) {
                    state.errors[errorKey] = error[errorKey];
                }
            }
        }
    },
    setSingleError(state, data) {
        state.errors[data.key] = data.value;
    }
};

export default {
    namespaced: true,
    state,
    getters,
    actions,
    mutations,
};
