import Vue from 'vue'
import VueRouter from 'vue-router'

import store from '../store'

Vue.use(VueRouter)

/**
 * roles: ['customer', 'relative', 'anonymous']
 */

const routes = [
  {
    path: '/',
    name: 'home',
    component: () => import('../views/pages/Home.vue'),
    meta: { title: 'Memorial web service' },
  },
  {
    path: '/about-us',
    name: 'about',
    component: () => import('../views/pages/About.vue'),
    meta: { title: 'About us' },
  },
  {
    path: '/auth/handle',
    name: 'auth.handle',
    component: () => import('../views/auth/Handle.vue'),
    meta: { title: 'Auth Handle', requiresAuth: false },
  },
  {
    path: '/memorials/create/welcome',
    name: 'memorials.create.welcome',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../views/memorials/create/Welcome.vue'),
    meta: { title: 'Welcome', requiresAuth: true, roles: ['customer'] },
  },
  {
    path: '/memorials/create/step1',
    name: 'memorials.create.step1',
    component: () => import('../views/memorials/create/Step1.vue'),
    meta: { title: 'Create memorial - step1', requiresAuth: true, roles: ['customer'] },
  },
  {
    path: '/memorials/create/step2',
    name: 'memorials.create.step2',
    component: () => import('../views/memorials/create/Step2.vue'),
    meta: { title: 'Create memorial - step2', requiresAuth: true, roles: ['customer'] },
  },
  {
    path: '/memorials/create/step3',
    name: 'memorials.create.step3',
    meta: { title: 'Create memorial - step3', requiresAuth: true, roles: ['customer'] },
    component: () => import('../views/memorials/create/Step3.vue')
  },
  {
    path: '/memorials/show', // :slug
    meta: {
      title: 'Memorials show',
      // requiresAuth: true
    },
    component: () => import('../views/memorials/show/Default.vue')
  },
  {
    path: '/memorials/:slug', // :slug
    name: 'memorials.show',
    meta: {
      title: 'Memorials show',
      // requiresAuth: true
    },
    component: () => import('../views/memorials/show/Index.vue')
  },
  {
    path: '/memorials/:slug/life/:period?',
    name: 'memorials.life',
    meta: {
      title: 'Life stories',
    },
    component: () => import('../views/memorials/show/Life.vue')
  },
  {
    path: '/memorials/:slug/relatives/:relative?',
    name: 'memorials.relatives',
    meta: {
      title: 'Relatives stories',
    },
    component: () => import('../views/memorials/show/Relatives.vue')
  },
  {
    path: '/memorials/:id/edit',
    name: 'memorials.edit',
    meta: {
      title: 'Memorials edit',
      requiresAuth: true,
      roles: ['customer'],
      type: 'memorial',
    },
    component: () => import('../views/memorials/edit/Index.vue')
  },
  {
    path: '/memorials/:id/edit/life/:period?',
    name: 'memorials.edit.life',
    meta: {
      title: 'Memorials edit life periods',
      requiresAuth: true,
      roles: ['customer'],
      type: 'memorial',
    },
    component: () => import('../views/memorials/edit/Life.vue')
  },
  {
    path: '/memorials/:id/edit2',
    name: 'memorials.edit2',
    meta: {
      title: 'Memorials edit2',
      requiresAuth: true,
      roles: ['customer'],
    },
    component: () => import('../views/memorials/edit/Index2.vue')
  },
  {
    path: '/memorials/:slug/share',
    name: 'memorials.story.auth',
    meta: { title: 'Create Story', requiresAuth: false },
    component: () => import('../views/memorials/story/Index.vue')
  },
  {
    path: '/memorials/:slug/story/create/:period?',
    name: 'memorials.story.create',
    meta: { title: 'Create Story', requiresAuth: true, roles: ['relative', 'customer'], redirectRoute: 'memorials.story.auth' },
    component: () => import('../views/memorials/story/Index.vue')
  },
  {
    path: '/memorials/:slug/story/share/:period?',
    name: 'memorials.story.share',
    meta: { title: 'Create Story', requiresAuth: false, },
    component: () => import('../views/memorials/story/Index')
  },
  {
    path: '/memorials/:slug/payment',
    name: 'memorials.payment',
    meta: { title: 'Memorials payment', requiresAuth: true, roles: ['customer'] },
    component: () => import('../views/memorials/Payment.vue')
  },
  {
    path: '/dashboard',
    name: 'dashboard.show',
    meta: {
      title: 'Dashboard',
      requiresAuth: true,
      roles: ['customer'],
      type: 'dashboard',
    },
    component: () => import('../views/dashboard/Index.vue')
  },
  {
    path: '/payment/success/:id',
    name: 'payment.success',
    component: () => import('../views/pages/SuccessPaid.vue'),
    meta: { title: 'Payment success' },
  },
  {
    path: '/payment/fail/:id',
    name: 'payment.fail',
    component: () => import('../views/pages/FailPaid.vue'),
    meta: { title: 'Payment fail' },
  },
  {
    path: '/life',
    name: 'life.life',
    meta: {
      title: 'Life story',
      // requiresAuth: true
    },
    component: () => import('../views/memorials/life/Life.vue')
  },
  {
    path: '/relatives',
    name: 'relatives',
    meta: {
      title: 'Relatives stories',
      // requiresAuth: true
    },
    component: () => import('../views/memorials/life/Relatives-stories.vue')
  },
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes,
  scrollBehavior (to, from, savedPosition) {
    return { x: 0, y: 0 }
  }
})

let redirectUrl = window.location.origin;

if(window.location.origin.indexOf('eternity.co.il') != -1) {
  let url = decodeURIComponent(window.location.origin +'/auth/handle');
  redirectUrl = 'https://eternity.co.il/sso?type=login&lang='+localStorage.getItem('vueml-lang')+'&redirectURL='+url;
}

// Check Route Access
router.beforeEach((to, from, next) => {
  document.title = to.meta.title

  if (typeof to.meta.roles !== 'undefined' && to.meta.roles.includes('customer') && to.meta.roles.length === 1 && store.getters['auth/isLoggedIn'](to.meta.roles)) {
    store.dispatch('profile/me');
  }

  if(to.matched.some(record => record.meta.requiresAuth)) {

    localStorage.setItem('redirectUrl', window.location.origin+to.path);

    if (typeof to.meta.roles !== 'undefined' && store.getters['auth/isLoggedIn'](to.meta.roles)) {
      next()
      return;
    }

    if(to.meta.hasOwnProperty('redirectRoute') && to.meta.redirectRoute.length > 0) {
      next({ name: to.meta.redirectRoute, params: to.params });
    } else {
      location.href = redirectUrl;
    }
  } else {
    if(window.location.pathname != '/' && window.location.pathname != '/auth/handle') {
      localStorage.removeItem('redirectUrl');
    }
    next()
  }
})

export default router
